import logo from './assets/devflix-logo.png';
import './App.css';
import React from 'react';
import IMAGES from './images';

function App() {
  React.useEffect(() => {
    var video = document.getElementById('video');
    var videoScrollThreshold = parseInt(getComputedStyle(document.getElementById('video-container')).height,10) * 0.75;

    video.volume = 0.1;

    window.addEventListener('scroll', () => {
      var header = document.getElementById("header-container");
      

      if(window.scrollY > 0) {
        header.style.backgroundColor = "var(--background-color)"
      }
      else {
        header.style.backgroundColor = "#00000000";
      }
    });

    var muteButton = document.getElementById("mute-button");

    muteButton.addEventListener('click', (e) => {
      e.stopPropagation();
      video.muted = !video.muted;

      if(video.muted) {
        muteButton.innerHTML = '<svg xmlns="http://www.w3.org/2000/svg" width="90%" height="90%" fill="#ffffff" className="bi bi-volume-mute" viewBox="0 0 16 16">' +
                                  '<path d="M6.717 3.55A.5.5 0 0 1 7 4v8a.5.5 0 0 1-.812.39L3.825 10.5H1.5A.5.5 0 0 1 1 10V6a.5.5 0 0 1 .5-.5h2.325l2.363-1.89a.5.5 0 0 1 .529-.06zM6 5.04 4.312 6.39A.5.5 0 0 1 4 6.5H2v3h2a.5.5 0 0 1 .312.11L6 10.96V5.04zm7.854.606a.5.5 0 0 1 0 .708L12.207 8l1.647 1.646a.5.5 0 0 1-.708.708L11.5 8.707l-1.646 1.647a.5.5 0 0 1-.708-.708L10.793 8 9.146 6.354a.5.5 0 1 1 .708-.708L11.5 7.293l1.646-1.647a.5.5 0 0 1 .708 0z"/>' +
                              '</svg>';
      }

      else {
        muteButton.innerHTML = '<svg xmlns="http://www.w3.org/2000/svg" style="padding-left: 15%" width="90%" height="90%" fill="#ffffff" className="bi bi-volume-down" viewBox="0 0 16 16">' +
                                  '<path d="M10.121 12.596A6.48 6.48 0 0 0 12.025 8a6.48 6.48 0 0 0-1.904-4.596l-.707.707A5.483 5.483 0 0 1 11.025 8a5.483 5.483 0 0 1-1.61 3.89l.706.706z"/>' +
                                  '<path d="M10.025 8a4.486 4.486 0 0 1-1.318 3.182L8 10.475A3.489 3.489 0 0 0 9.025 8c0-.966-.392-1.841-1.025-2.475l.707-.707A4.486 4.486 0 0 1 10.025 8zM7 4a.5.5 0 0 0-.812-.39L3.825 5.5H1.5A.5.5 0 0 0 1 6v4a.5.5 0 0 0 .5.5h2.325l2.363 1.89A.5.5 0 0 0 7 12V4zM4.312 6.39 6 5.04v5.92L4.312 9.61A.5.5 0 0 0 4 9.5H2v-3h2a.5.5 0 0 0 .312-.11z"/>' +
                              '</svg>';
      }
    });

    var videoContainer = document.getElementById("video-container");
    var firstTime = true;

    videoContainer.addEventListener('click', () => {
      var placeholder = document.getElementById('video-placeholder');
      placeholder.style.opacity = "0";

      var overlayText = document.getElementById("overlay-text");
      var overlayLogo = document.getElementById("overlay-logo");

      overlayText.style.maxHeight = 0;
      overlayText.style.marginBottom = 0;
      overlayLogo.style.width = "40%";

      muteButton.style.visibility = "visible";

      var playOverlay = document.getElementById("play-overlay");

      if(video.paused){
        video.play();
        playOverlay.style.display = "none";
        if(firstTime){
          muteButton.click();
          firstTime = false;
        }
      }
      else {
        video.pause();
        playOverlay.style.display = "";
      }

      window.addEventListener('scroll', () => {
        var header = document.getElementById("header-container");
        
  
        if(window.scrollY > 0) {
          header.style.backgroundColor = "var(--background-color)"
        }
        else {
          header.style.backgroundColor = "#00000000";
        }
  
        if (window.scrollY >= videoScrollThreshold) {
          if(!video.paused){
            video.pause();
            playOverlay.style.display = "";
          }
          if(!video.muted)
            muteButton.click();
        }
      });
    });

    var sliderContentList = document.getElementsByClassName("scroll-slider-content");

    for (let sc of sliderContentList) {
      sc.addEventListener('scroll', () => {
        var scrollLeft = sc.scrollLeft;
        var scrollWidth = sc.scrollWidth;
        var offsetWidth = sc.offsetWidth;
        var contentWidth = scrollWidth - offsetWidth;

        if (scrollLeft === 0) {
          sc.parentElement.getElementsByClassName("slider-navigation left-button")[0].style.display = "none";
        }

        if (contentWidth <= scrollLeft)
        {
          sc.parentElement.getElementsByClassName("slider-navigation right-button")[0].style.display = "none";
        }
      })
    }
  });

  return (
    <div className='main-container'>
      <Header  />
      <Trailer />
      <Sliders />
    </div>
  );
}

class Header extends React.Component {
  render() {
    return (
      <div id='header-container' className='header-container'>
        <div id='header' className='header'>
          <img className='header-logo' src={logo} alt='Devflix' />
          <HeaderLinks />
        </div>
      </div>
    )
  }
}

class HeaderLinks extends React.Component {
  render() {
    return (
      <div className='header-links'>
        <div className='selected-link'>Inicio</div>
        <div>Series TV</div>
        <div>Películas</div>
        <div>Novedades más vistas</div>
        <div>Mi lista</div>
        <div>Volver a ver</div>
      </div>
    )
  }
}

class Trailer extends React.Component {
  render() {
    return (
      <div id='video-container' className='video-container'>
        <video id='video' width='100%' loop muted >
          <source src={require('./assets/trailer.mp4')} type='video/mp4' />
          Your browser does not support the video tag.
        </video>
        <div id='video-placeholder' className='video-placeholder'>
          <img src={require('./assets/placeholder.jpg')} alt="placeholder" />
        </div>
        <Overlay />
        <div id='play-overlay' className='play-overlay'>
          <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" fill="#ffffffa0" className="bi bi-play-circle" viewBox="0 0 16 16">
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
            <path d="M6.271 5.055a.5.5 0 0 1 .52.038l3.5 2.5a.5.5 0 0 1 0 .814l-3.5 2.5A.5.5 0 0 1 6 10.5v-5a.5.5 0 0 1 .271-.445z"/>
          </svg> 
        </div>
        <div className='video-info'>
          <div id='mute-button' className='mute-button'>
            <svg xmlns="http://www.w3.org/2000/svg" width="90%" height="90%" fill="#ffffff" className="bi bi-volume-mute" viewBox="0 0 16 16">
              <path d="M6.717 3.55A.5.5 0 0 1 7 4v8a.5.5 0 0 1-.812.39L3.825 10.5H1.5A.5.5 0 0 1 1 10V6a.5.5 0 0 1 .5-.5h2.325l2.363-1.89a.5.5 0 0 1 .529-.06zM6 5.04 4.312 6.39A.5.5 0 0 1 4 6.5H2v3h2a.5.5 0 0 1 .312.11L6 10.96V5.04zm7.854.606a.5.5 0 0 1 0 .708L12.207 8l1.647 1.646a.5.5 0 0 1-.708.708L11.5 8.707l-1.646 1.647a.5.5 0 0 1-.708-.708L10.793 8 9.146 6.354a.5.5 0 1 1 .708-.708L11.5 7.293l1.646-1.647a.5.5 0 0 1 .708 0z"/>
            </svg>
          </div>
          <div className='age-rating'>
            <div className='age-container'>
              12+
            </div>
          </div>
        </div>
      </div>
    )
  }
}

class Overlay extends React.Component {
  render() {
    return (
      <div id='video-overlay' className='video-overlay'>
          <div className='overlay-logo'>
            <img id='overlay-logo' src={require('./assets/show-logo.png')} alt="logo" className='overlay-logo'/>
          </div>
          <div id='overlay-text' className='overlay-text'>
            <div className='overlay-text-header'>
              TRAILER EXE
            </div>
            <div className='overlay-text-body'>
              Here is a Battlefield 2042 meme trailer. With the new battlefield 2042 gameplay trailer dropping soon...
            </div>
          </div>
          <div className='overlay-buttons'>
            <div className='play-button'>
              <div className='button-icon'>
                <svg width="100%" height="100%" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" ><path d="M4 2.69127C4 1.93067 4.81547 1.44851 5.48192 1.81506L22.4069 11.1238C23.0977 11.5037 23.0977 12.4963 22.4069 12.8762L5.48192 22.1849C4.81546 22.5515 4 22.0693 4 21.3087V2.69127Z" fill="#000000"></path></svg>
              </div>
              Reproducir
            </div>
            <div className='info-button'>
              <div className='button-icon'>
                <svg width="100%" height="100%" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12 3C7.02944 3 3 7.02944 3 12C3 16.9706 7.02944 21 12 21C16.9706 21 21 16.9706 21 12C21 7.02944 16.9706 3 12 3ZM1 12C1 5.92487 5.92487 1 12 1C18.0751 1 23 5.92487 23 12C23 18.0751 18.0751 23 12 23C5.92487 23 1 18.0751 1 12ZM13 10V18H11V10H13ZM12 8.5C12.8284 8.5 13.5 7.82843 13.5 7C13.5 6.17157 12.8284 5.5 12 5.5C11.1716 5.5 10.5 6.17157 10.5 7C10.5 7.82843 11.1716 8.5 12 8.5Z" fill="#ffffff"></path></svg>
              </div>
              Más información
              </div>
          </div>
        </div>
    )
  }
}

var headers = {
  "Mi lista" : -1,
  "Top 10 más populares" : 10,
  "Popular en Devflix" : -1,
  "Esto de las listas": -1,
  "Se está yendo de las manos": -1,
  "Pero así es la vida": -1
};

class Sliders extends React.Component {
  render() {
    return (
      <div>
        <div className='sliders-background'>
        </div>
        <GetSliders headers={headers} />
      </div>
    )
  }
}

function GetSliders(props) {
  var headers = Object.keys(props.headers);
  const listItems = headers.map((header) => 
    <div key={header} className='slider-container'>
      <div className='slider-header'>
        {header}
      </div>
        <SetSliders length={props.headers[header]} />
    </div>
  )
    return(
      <div className='sliders-container'>{listItems}</div>
    )
}

function SetSliders(props){
  var arrayLength;

  if(props.length > 0)
    arrayLength = props.length;
  else
    arrayLength = Math.floor(Math.random() * (30 - 17 + 1)) + 17;

  var length = Array.apply("", Array(arrayLength));
  var image;

  for (let l of length) {
    image = Math.floor(Math.random() * (IMAGES.length));
    
    while (length.indexOf(IMAGES[image]) > -1) {
      image = Math.floor(Math.random() * (length.length - 0));
    }

    length[length.indexOf(l)] = IMAGES[image];
  }

  const listItems = length.map((l) =>
    <img key={"key" + l} className='slide' src={l} alt="" />
  )
  return (
    <div className='fixed-slider-content'>
      <div className='scroll-slider-content'>
        {listItems}
      </div>
      <button className='slider-navigation left-button'
      onClick={e => {
        var scroller = document.getElementsByClassName("scroll-slider-content")[0];
        var firstSlide = document.getElementsByClassName("slide")[0];
        var rightButton = document.getElementsByClassName("slider-navigation right-button")[0];
        var scrollDistance = scroller.clientWidth - parseInt(getComputedStyle(rightButton).width, 10) - parseInt(getComputedStyle(firstSlide).marginLeft, 10);

        var button = e.target;

        if (button.className !== "slider-navigation left-button")
          button = button.parentElement;

        button = button.nextElementSibling;
        button.style.display = "block";

        var parent = e.target.parentElement;

        while (parent.className !== "fixed-slider-content")
          parent = parent.parentElement;

        parent.firstChild.scrollLeft -= scrollDistance;
      }}>
        <i className='bi bi-chevron-compact-left'></i>
      </button>
      <button className='slider-navigation right-button'
       onClick={e => {  
        var scroller = document.getElementsByClassName("scroll-slider-content")[0];
        var firstSlide = document.getElementsByClassName("slide")[0];
        var rightButton = document.getElementsByClassName("slider-navigation right-button")[0];
        var scrollDistance = scroller.clientWidth - Math.round(parseFloat(getComputedStyle(rightButton).width), 10) - Math.round(parseFloat(getComputedStyle(firstSlide).marginLeft), 10);


        var button = e.target;

        if (button.className !== "slider-navigation right-button")
          button = button.parentElement;

        button = button.previousElementSibling;
        button.style.display = "block";

        var parent = e.target.parentElement;

        while (parent.className !== "fixed-slider-content")
          parent = parent.parentElement;

          parent.firstChild.scrollLeft += scrollDistance;
      }}>
      <i className='bi bi-chevron-compact-right'></i>
      </button>
    </div>
  )
}

export default App;
